body {
  margin: 0;
}

.stars {
  display: flex;

  &__star {
    background-image: url("./images/star.svg");
    background-repeat: no-repeat;
    background-position: center;
    height: 16px;
    width: 16px;
    margin-right: 4px;
  }
  &--1 > :first-child,
  &--2 > :nth-child(-n+2),
  &--3 > :nth-child(-n+3),
  &--4 > :nth-child(-n+4),
  &--5 > :nth-child(-n+5) {
    background-image: url("./images/star-active.svg");
  }
}
